.card-image {
    position: relative;
    margin-bottom: 15px;
}
.card-image img {
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

.card-image .front{
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front{
    transform: rotateY(0deg);
    transition-delay: 0.2s ;
}

.card-image .back{
    transition: all easy-in 0.2s;
    transition-delay: 0.2s;
}
.flipped .back{
    transform: rotateY(90deg);
    transition-delay: 0s;
}

@media only screen and (max-width: 600px) {
    .flipped .front{
        width: 80%;
    }
}