.chatInput {
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    width: 100%;
}

.chatInput > form {
    position: relative;
    display: flex;
    justify-content: center;
    width: 90%;
}

.chatInput > form > input {
    bottom: 30px;
    width: 100%;
    border: 1px solid gray;
    border-radius: 3px;
    padding: 20px;
}

.chatInput > form > div {
    width: 100%;
}

@media (max-width: 768px) {
    .chatInput > form {
        margin-top: 100px;
    }
}