.poker-cards-main {
    height: 100vh;
  }
  .button {
    background: none;
    border: 2px solid #fff;
    padding: 6px;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    font-size: 1em;
    margin-bottom: 40px;
    text-decoration: unset;
    height: 2.3rem;
    margin-right: 10px;
  }
  .button:hover {
    background: #c23866;
    color: #fff;
  }
  
  .card-grid {
    width: 80%;
    margin-top: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    margin: 0 auto;
  }
  